'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { updateUserRole } from './actions'

type Profile = {
  id: string
  first_name: string
  last_name: string
  role_id: string | null
  role: { role_name: string } | null
}

type Role = {
  id: string
  role_name: string
  company: { company_name: string } | null
}

interface RosterClientProps {
  initialProfiles: Profile[]
  roles: Role[]
}

export default function RosterClient({ initialProfiles, roles }: RosterClientProps) {
  const router = useRouter()
  const [searchTerm, setSearchTerm] = useState('')
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [selectedProfile, setSelectedProfile] = useState<Profile | null>(null)
  const [selectedRoleId, setSelectedRoleId] = useState<string>('')
  const [isSaving, setIsSaving] = useState(false)

  // --- FILTERING ---
  const filteredProfiles = initialProfiles.filter(p => 
    p.last_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    p.first_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (p.role?.role_name || '').toLowerCase().includes(searchTerm.toLowerCase())
  )

  // Group roles for the dropdown
  const rolesByCompany = roles.reduce((acc, role) => {
    const companyName = role.company?.company_name || 'Unassigned'
    if (!acc[companyName]) acc[companyName] = []
    acc[companyName].push(role)
    return acc
  }, {} as Record<string, Role[]>)

  // --- HANDLERS ---
  const handleEditClick = (profile: Profile) => {
    setSelectedProfile(profile)
    setSelectedRoleId(profile.role_id || '')
    setIsModalOpen(true)
  }

  const handleSave = async () => {
    if (!selectedProfile) return
    setIsSaving(true)
    
    // Convert empty string back to null for database
    const roleIdToSave = selectedRoleId === '' ? null : selectedRoleId
    
    const result = await updateUserRole(selectedProfile.id, roleIdToSave)
    
    if (result.error) {
      alert(`Error: ${result.error}`)
    } else {
      setIsModalOpen(false)
      router.refresh() // Refresh server data
    }
    setIsSaving(false)
  }

  return (
    <div className="space-y-6">
      {/* --- SEARCH BAR --- */}
      <div className="relative">
        <input
          type="text"
          placeholder="Search cadets..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="block w-full rounded-md border-gray-300 dark:border-gray-600 dark:bg-gray-900 dark:text-white shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm py-3 px-4"
        />
      </div>

      {/* --- ROSTER TABLE --- */}
      <div className="bg-white dark:bg-gray-800 shadow rounded-lg overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
          <thead className="bg-gray-50 dark:bg-gray-900/50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">Name</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">Current Role</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">Action</th>
            </tr>
          </thead>
          <tbody className="bg-white dark:bg-gray-800 divide-y divide-gray-200 dark:divide-gray-700">
            {filteredProfiles.map((profile) => (
              <tr key={profile.id} className="hover:bg-gray-50 dark:hover:bg-gray-700/50 transition-colors">
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900 dark:text-white">
                  {profile.last_name}, {profile.first_name}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 dark:text-gray-300">
                  {profile.role?.role_name || <span className="text-gray-400 italic">Unassigned</span>}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <button 
                    onClick={() => handleEditClick(profile)}
                    className="text-indigo-600 dark:text-indigo-400 hover:text-indigo-900 dark:hover:text-indigo-300"
                  >
                    Assign Role
                  </button>
                </td>
              </tr>
            ))}
            {filteredProfiles.length === 0 && (
              <tr>
                <td colSpan={3} className="px-6 py-8 text-center text-gray-500 dark:text-gray-400">
                  No cadets found.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* --- EDIT MODAL --- */}
      {isModalOpen && selectedProfile && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm p-4">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-xl max-w-md w-full border dark:border-gray-700 flex flex-col max-h-[90vh]">
            
            <div className="p-6 border-b dark:border-gray-700">
              <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                Assign Role: {selectedProfile.last_name}, {selectedProfile.first_name}
              </h3>
            </div>
            
            <div className="p-6 overflow-y-auto">
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Select Role</label>
              <select
                value={selectedRoleId}
                onChange={(e) => setSelectedRoleId(e.target.value)}
                className="block w-full rounded-md border-gray-300 dark:border-gray-600 dark:bg-gray-900 dark:text-white shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
              >
                <option value="">-- No Role (Unassigned) --</option>
                {Object.entries(rolesByCompany).map(([company, companyRoles]) => (
                  <optgroup key={company} label={company}>
                    {companyRoles.map(role => (
                      <option key={role.id} value={role.id}>
                        {role.role_name}
                      </option>
                    ))}
                  </optgroup>
                ))}
              </select>
            </div>

            <div className="p-6 border-t dark:border-gray-700 flex justify-end gap-3 bg-gray-50 dark:bg-gray-900/30 rounded-b-lg">
              <button
                onClick={() => setIsModalOpen(false)}
                className="px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700"
                disabled={isSaving}
              >
                Cancel
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 disabled:bg-gray-400"
                disabled={isSaving}
              >
                {isSaving ? 'Saving...' : 'Save Assignment'}
              </button>
            </div>

          </div>
        </div>
      )}

    </div>
  )
}